import React, { useCallback, useEffect, useState } from 'react';
import { connect, useDispatch } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';
import { first, take } from 'rxjs/operators';
import { documentation1, issueIcon, noColleaguesIllustration, reset2 } from '../../../assets/images/icons';
import { setOnBoardingModalsVisibility, setOnBoardingVisible } from '../../../reduxStore/actions/modalsActions';
import { getAllUsers, getFilteredUsers, getUnassignedUsers } from '../../../services/user.service';
import { LeftSideFilter, Spinner, PermissionWrapper, po } from '../../common';
import UserCard from '../../common/UserCard/UserCard';
import Button from '../../common/UI/Button/Button';
import cls from './Company.module.scss';
import UserOverviewModal from '../CompanyComps/UserOverviewModal';
import CreateUsers from '../../OnboardingEntries/OnboardingModals/CreateUsers';
import HeaderTopBar from '../../common/HeaderTopBar/HeaderTopBar';
import { setShowMenu } from '../../../reduxStore/actions/flowActions';
const R = require('ramda');
const randomColor = require('randomcolor');

function getRandomInt(min, max) {
	min = Math.ceil(min);
	max = Math.floor(max);
	return Math.floor(Math.random() * (max - min)) + min;
}
const randomNumberString = String(getRandomInt(10, 245));
export const randomisedColor = { color: `rgb(${randomNumberString},${randomNumberString},${randomNumberString})` || randomColor({}) };
const Users = props => {
	const location = useLocation();
	const { user, onToggleModalVisibility, onSetOnBoardingVisible, companyUsersArray } = props;
	const [apiRequest, setApiRequest] = useState(false);
	const [usersArray, setUsersArray] = useState(companyUsersArray);
	const [unassignedUsersArray, setUnassignedUsersArray] = useState([]);
	const [noColleagues, setNoCollegues] = useState(false);
	const [colleagueModalVisible, setColleagueModalVisible] = useState(false);
	const [addUsers, setAddUsers] = useState(false);
	const [activeItem, setActiveItem] = useState('');
	const [filteredIds, setFilteredIds] = useState([]);
	const [filterData, setFilterData] = useState({ filterActive: false, departments_filteredIds: [], positions_filteredIds: [] });
	const [processingResetFilters, setProcessingResetFilters] = useState(false);
	const [loading, setLoading] = useState(false);
	const settingLoading = val => setApiRequest(val);
	const dispatch = useDispatch();
	const navigate = useNavigate();
	useEffect(() => {
		settingLoading(true);
		getUnassignedUsers()
			.pipe(first())
			.subscribe({
				next: data => {
					setUnassignedUsersArray(data);
				},
			});

		dispatch(setShowMenu(true));
	}, []);

	useEffect(() => {
		const item = usersArray?.find(i => i.id == new URLSearchParams(location?.search).get('userId'));
		if (item) {
			openTaskModal(item);
		}
	}, [location?.search, usersArray]);

	// useEffect(() => {
	// 	retreiveUsers({ filtering: filterData.filterActive ? true : false });
	// }, [filteredIds]);

	useEffect(() => {
		if (filterData.filterActive) {
			const newFilteredIds = [...filterData.departments_filteredIds, ...filterData.positions_filteredIds];
			if (newFilteredIds?.length > 0) {
				setFilteredIds([...newFilteredIds]);
			} else {
				onResetFiltersClick();
			}
		}
	}, [filterData]);
	const retreiveUsers = useCallback(({ filtering }) => {
		settingLoading(true);
		if (filtering) {
			getFilteredUsers([...R.uniq(filteredIds)])
				.pipe(first())
				.subscribe({
					next: data => {
						setUsersArray([...data]);
						setApiRequest(false);
					},
					error: _error => {
						setApiRequest(false);
					},
				});
		} else {
			getAllUsers()
				.pipe(take(1))
				.subscribe({
					next: data => {
						if (data.length === 0) {
							setNoCollegues(true);
							setApiRequest(false);
						} else {
							setUsersArray([...data]);
							setApiRequest(false);
							setApiRequest(false);
						}
					},
					error: _error => {
						setApiRequest(false);
					},
					complete: () => {
						setApiRequest(false);
					},
				});
		}
	}, []);

	const openTaskModal = item => {
		// setActiveItem(item);
		// setColleagueModalVisible(true);
		// window.scrollTo(0, 0);

		navigate('/user-profile', { state: { user: item } });
	};
	const handleClose = () => {
		setColleagueModalVisible(false);
	};

	const onFilterByDepartments = useCallback(
		({ _index, state, item }) => {
			let newFilteredIds = [...filterData.positions_filteredIds];

			if (state) {
				newFilteredIds = [...filterData.departments_filteredIds, ...item.user_found] ?? [];
			} else {
				item.user_found.forEach(e => newFilteredIds.splice(newFilteredIds.indexOf(newFilteredIds.find(i => i === e)), 1));
			}
			setFilterData(c => ({ ...c, filterActive: true, departments_filteredIds: [...newFilteredIds] }));
		},
		[filterData]
	);

	const onFilterByPosition = useCallback(
		({ _index, state, item }) => {
			let newFilteredIds = [...filterData.positions_filteredIds];

			if (state) {
				newFilteredIds = [...filterData.positions_filteredIds, ...item.user_found] ?? [];
			} else {
				item.user_found.forEach(e => newFilteredIds.splice(newFilteredIds.indexOf(newFilteredIds.find(i => i === e)), 1));
			}
			setFilterData(c => ({ ...c, filterActive: true, positions_filteredIds: [...newFilteredIds] }));
		},
		[filterData]
	);

	const onAddUserClick = () => {
		setAddUsers(true);
	};
	const onResetFiltersClick = async () => {
		setProcessingResetFilters(true);
		setUsersArray(companyUsersArray);
		setProcessingResetFilters(false);
		setFilterData(c => ({
			...c,
			filterActive: false,
			positions_filteredIds: [],
			departments_filteredIds: [],
		}));
	};
	return (
		<div className={cls.usersHost}>
			{noColleagues && (
				<div className={cls.noColleaguesSection}>
					<div className={cls.noColleaguesBackground}>
						<div className={cls.noColleaguesContainer}>
							<div className={cls.noColleaguesInnerContainer}>
								<img src={issueIcon} alt={''} />
								<div className={cls.textSection}>
									<div className={cls.topText}>
										<p style={{ fontFamily: 'var(--app-font-poppins-bold)' }}>{`${unassignedUsersArray.length} colleagues`}</p>
										<p style={{ paddingLeft: '5px' }}>{'don’t have any Positions assigned.'}</p>
									</div>
									<div className={cls.bottomText}>
										{unassignedUsersArray?.length > 0 &&
											unassignedUsersArray.map((item, index) => {
												return (
													<div key={index} style={{ display: 'flex', flexDirection: 'row' }}>
														<p>{item.first_name + ' ' + item.last_name?.match(/\b(\w)/g) + '.,' + ' '}</p>
													</div>
												);
											})}
										{unassignedUsersArray?.length > 2 && <p>{`+${unassignedUsersArray.length - 2} others`}</p>}
										<p
											style={{
												fontFamily: 'var(--app-font-quicksand-medium)',
												paddingLeft: '5px',
											}}
										>
											{' don’t have any positions at this moment.'}
										</p>
									</div>
								</div>
							</div>
							<div>
								<Button
									title="Fix Issue"
									clicked={() => {
										onToggleModalVisibility({ eightModalVisible: true, hideSkipBtn: true });
										onSetOnBoardingVisible(true);
										window.scrollTo(0, 0);
									}}
								/>
							</div>
						</div>
					</div>
				</div>
			)}
			<HeaderTopBar headerTitle={'Users'} targetProcessing={false} />
			{apiRequest && (
				<span className={cls.loadingIndicator}>
					<Spinner type={'BallTriangle'} color="var(--app-color-gray-dark)" height={30} width={`${30}px`} />
				</span>
			)}
			<br />
			<div style={{ display: 'flex' }}>
				<Button title={'Add Users'} clicked={onAddUserClick} style={{ width: '150px' }} />
				<Button
					//title={'Refresh List'}
					light
					clicked={() => {
						setLoading(true);
						getAllUsers(true)
							.pipe(take(1))
							.subscribe({
								next: data => {
									if (data.length === 0) {
										setNoCollegues(true);
										setLoading(false);
									} else {
										setUsersArray([...data]);
										setLoading(false);
									}
								},
								error: _error => {
									setLoading(false);
								},
								complete: () => {
									setLoading(false);
								},
							});
					}}
					icon={<img src={reset2} />}
					loading={loading}
				/>
			</div>
			<br />
			{noColleagues ? (
				<div className={cls.noTeamMembers}>
					<img src={noColleaguesIllustration} alt={''} />
					<p>{'There are currently no team members.'}</p>
					<Button
						title={'Add Users'}
						clicked={() => {
							onToggleModalVisibility({ eightModalVisible: true, hideSkipBtn: true });
							onSetOnBoardingVisible(true);
							window.scrollTo(0, 0);
						}}
					/>
				</div>
			) : (
				<div className={cls.colleaguesSection}>
					<div className={cls.leftSideContainer} style={{ display: 'none' }}>
						<PermissionWrapper permissions={[po.company.filter]}>
							<LeftSideFilter
								filterFor={'users'}
								topButton={{
									tb_title: 'Add Users',
									tb_className: cls.filterButton,

									tb_iconClassName: cls.filterButtonIcon,
									tb_clicked: onAddUserClick,
								}}
								onFilters={{
									department: { onFilterByDepartments: onFilterByDepartments },
									position: { onFilterByPosition: onFilterByPosition },
								}}
								resetButton={{
									rb_title: 'Reset Filters',
									rb_className: cls.resetFilterButton,
									rb_icon: documentation1,
									rb_iconClassName: cls.resetFilterButtonIcon,
									rb_clicked: onResetFiltersClick,
									rb_loading: processingResetFilters,
									rb_loaderRadius: 20,
									rb_filteringActive: filterData.filterActive,
								}}
							/>
						</PermissionWrapper>
					</div>
					<div className={cls.rightSideContainer}>
						{usersArray?.length > 0 &&
							usersArray?.map((item, index) => {
								return (
									<div key={index} style={{ padding: '10px' }}>
										<UserCard
											photo={item.photo}
											firstName={item?.first_name}
											lastName={item?.last_name}
											position={item?.positions?.slice(0, 1)?.map(pos => {
												return pos.name;
											})}
											email={item?.email}
											cardClicked={() => openTaskModal(item)}
										/>
									</div>
								);
							})}
					</div>
				</div>
			)}
			{colleagueModalVisible && <UserOverviewModal isVisible={colleagueModalVisible} activeItem={activeItem} handleClose={handleClose} isAdmin={user.is_admin} />}
			{addUsers && (
				<CreateUsers
					hasTutorial
					modalVisible={addUsers}
					closeButtonAction={() => setAddUsers(false)}
					backButtonAction={() => setAddUsers(false)}
					shadowedContainerStyle={{ minWidth: '1110px', minHeight: '725px' }}
					modalWrapperStyle={{ minWidth: '1110px', minHeight: '725px' }}
					onContinueButtonAction={() => {
						setAddUsers(false);
					}}
					onCancel={() => setAddUsers(false)}
				/>
			)}
		</div>
	);
};

const mapStateToProps = store => {
	const {
		authenticatedUser: { position },
	} = store.authReducer;
	const { user } = store.sessionReducer;

	const { companyData, companyPositionsArray, allTasks, allCompletedTasks, allCompletedTasksPaginated, companyUsersArray } = store.flowReducer;
	return {
		companyData,
		companyPositionsArray,
		position,
		allTasks,
		allCompletedTasks,
		allCompletedTasksPaginated,
		user,
		companyUsersArray,
	};
};

const mapDispatchToProps = dispatch => {
	return {
		onSetOnBoardingVisible: visible => dispatch(setOnBoardingVisible(visible)),
		onToggleModalVisibility: modalVisible => dispatch(setOnBoardingModalsVisibility(modalVisible)),
	};
};
export default connect(mapStateToProps, mapDispatchToProps)(Users);
